<template>
    <div class="submit">
        <div class="login_header">
          <img src="../../assets/shopImage/submit.png" alt="">
        </div>
        <div class="login_main">
            <label class="form">
              <img src="../../assets/shopImage/username.png" alt="">
              <input type="text" class="input" v-model.trim="username" placeholder='请输入手机号' autocomplete="username">
            </label>
            <label class="form">
              <img src="../../assets/shopImage/password.png" alt="">
              <input type="password" class="input" v-model.trim="password" placeholder="请输入密码" autocomplete="password">
            </label>
          <button class="login_btn" @click="submit">登录</button><br>
          <p class="login_r">
            <a href="javascript:void(0)" @click="login" class="login_forget">还没有账号？<span style="color: #ff6f00;font-size: 0.3rem">注册</span></a>
            <a href="javascript:void(0)" class="login_forget" @click="forget">忘记密码?</a>
          </p>
        </div>
    </div>
</template>
<script>
  import { Toast} from 'mint-ui';
  import { Indicator } from 'mint-ui';
  export default{
    data(){
        return {
          username: '',
          password: ''
        }
    },
    methods:{
      //登录
      submit(){
        if (this.check.isPoneAvailable(this.username) && this.check.isPassWordAvailable(this
            .password)) {
          Indicator.open();
          this.post('home/Login/login', {
            username: this.username,
            password: this.password
          }).then(res => {
            Indicator.close();
            if (res.data.code === 10000) {
              Toast({
                message: res.data.msg,
                duration: 1000
              });
              let json = {
                name: this.username,
                word: this.password
              };
              localStorage.setItem('token', res.data.token);
              let expire = Date.now() + res.data.time * 1000;
              localStorage.setItem('expires', expire);
              localStorage.setItem('user', JSON.stringify(json));
              this.$router.push('/index');
            } else Toast(res.data.msg)
          })
        } else {
          Toast('手机号或密码格式错误');
        }
      },
      //注册
      login(){
          this.$router.push('/login')
      },
      forget(){
        this.$router.push('/forget')
      }
    },
    mounted(){
      let json = JSON.parse(localStorage.getItem('user'));
      if (json) {
          this.username = json.name;
          this.password = json.word;
      }
    }
  }
</script>
<style scoped>
@import "../../../static/login.css";
</style>



